@font-face {
  font-family: "Merienda", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Merienda&family=Raleway:wght@300&display=swap");
  font-family: "Raleway", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap");
}
body {
  background-color: #272424;
  color: white;
  margin: 0px;
}

.part {
  padding: 20px 90px;
}

.part-title {
  color: #4f5b66;
  font-size: 2em;
  margin-bottom: 30px;
}

a:hover {
  color: #333331;
  transition: 0.4s ease;
}

header {
  width: 100%;
  position: fixed;
  background-color: rgba(2, 2, 2, 0.5);
  z-index: 5;
}
header #navbar .svg-inline--fa {
  display: none;
}
header ul {
  display: flex;
}
header ul li {
  flex-direction: row;
  list-style-type: none;
  padding: 14px 25px;
  font-size: 24px;
}
header ul a {
  text-decoration: none;
  color: white;
}
header #link {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  z-index: 1;
  top: 350px;
  left: 15px;
}
header #link hr.vertical {
  width: 2px;
  height: 120px;
  color: white;
  border-right: 1px dashed white;
  border-top: 0;
  border-left: 0;
  border-bottom: 0;
}
header .social {
  font-size: 25px;
  margin: 10px 15px;
  color: whitesmoke;
}
header .social:hover {
  color: #333331;
  transition: 0.4s ease;
}

#home {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.97), rgba(0, 0, 0, 0)), bottom no-repeat url("../gulp/img/nature2.webp");
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
  z-index: 0;
}
#home #home-title h1 {
  font-family: "Raleway", sans-serif;
  margin-top: 12rem;
  font-size: 4em;
}
#home #home-title h2 {
  text-align: center;
}
#home #home-moreInfo {
  margin-top: 20rem;
  display: flex;
  flex-direction: column;
  text-align: center;
}
#home #home-moreInfo #home-button {
  text-decoration: none;
  font-weight: bold;
  font-size: 1.2em;
  line-height: 2rem;
  color: #807c7c;
}
#home #home-moreInfo .fa-chevron-down {
  margin: auto;
  color: #807c7c;
}
#home #home-moreInfo:hover {
  cursor: pointer;
  transform: translateY(-5px);
  transition: 0.4s ease;
}
#home #home-link {
  float: right;
  position: fixed;
  margin-top: 40rem;
  margin-left: 77rem;
}
#home #home-link a {
  background-color: rgba(37, 31, 31, 0.3);
  color: white;
  border-radius: 50%;
  font-size: larger;
  padding: 1.5em;
}
#home #home-link a:hover {
  background-color: #251f1f;
}

#about {
  background-color: #0b0b0c;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#about-container {
  display: flex;
  flex-direction: row;
}
#about-container img {
  filter: drop-shadow(5px 5px 55px #313030);
}

#about-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 7vw 0 7vw;
  font-size: larger;
  line-height: 2em;
}

#projects {
  background-color: #1a191c;
}

#projects-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.project {
  background-color: #292b2e;
  border: 1px solid #464442;
  justify-content: space-between;
  text-align: center;
  border-radius: 10px;
  min-width: 300px;
  margin: 2rem;
}
.project h3 {
  font-size: 2em;
}
.project p {
  margin-top: 35px;
}
.project .items {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  height: 70px;
  padding-top: 120px;
}
.project .items .svg-mongo {
  padding-top: 15px;
}
.project .items .svg-express {
  padding-top: 15px;
}
.project .items .svg-js {
  padding-top: 15px;
}
.project .items .fa-sass {
  font-size: 3em;
  padding-top: 20px;
  color: #ff0d4f;
}
.project .items .fa-gulp {
  font-size: 3em;
  padding-top: 20px;
  width: 65px;
  color: red;
}
.project .items .fa-php {
  font-size: 3em;
  padding-top: 20px;
  color: #1f5fe9;
}
.project .items .fa-wordpress {
  font-size: 3em;
  padding-top: 20px;
  color: #1f57cf;
}
.project .items .fa-react {
  font-size: 3em;
  padding-top: 20px;
  color: #1665be;
}
.project .items .fa-node-js {
  font-size: 3em;
  padding-top: 20px;
  width: 65px;
  color: #1ee417;
}
.project .project-link-container {
  display: flex;
  justify-content: space-around;
  margin: 3rem 0rem;
}
.project .project-link-container a {
  color: #c0c5ce;
  font-size: 1.5em;
}
.project .project-link-container .project-link-git:hover > .fa-folder-open {
  display: block;
}
.project .project-link-container .project-link-git:hover > .fa-folder {
  display: none;
}
.project .project-link-container .fa-folder-open {
  display: none;
}
.project .project-link-container .project-link-net:hover > .fa-sign-out-alt {
  display: block;
}
.project .project-link-container .project-link-net:hover > .fa-sign-in-alt {
  display: none;
}
.project .project-link-container .fa-sign-out-alt {
  display: none;
}

.project:hover {
  border: 1px solid #0085dd;
  transform: scale3d(1.1, 1.1, 1.1);
  transition: all 0.5s ease;
  box-shadow: -5px 5px 10px 10px #000000;
}

#contact, footer {
  background-color: #0b0b0c;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#contact {
  height: 90vh;
}

#contacts-container {
  display: flex;
  flex-direction: row;
}

#contacts-container-left, #contacts-container-right {
  width: 50%;
}

#contacts-container-left {
  padding-left: 50px;
}

.contact-box {
  background-color: rgba(255, 255, 255, 0.06);
  border-right: 0.7px solid #0085dd;
  border-bottom: 0.7px solid #0085dd;
  border-radius: 45px 0px 45px;
  text-align: center;
  margin: 20px;
  padding: 10px;
  width: 250px;
}
.contact-box h4 {
  color: #4f5b66;
  font-size: 1.2em;
  margin-top: 5px;
}
.contact-box a {
  color: white;
  text-decoration: none;
  font-size: 1.2em;
}

.contact-box:hover {
  transform: translateX(30px);
  transition: all 1s ease;
  background-color: #a7adba;
}
.contact-box:hover a {
  color: #0085dd;
}

#contacts-container-right {
  text-align: center;
}
#contacts-container-right h3 {
  font-size: 2em;
}

#contacts-container-right-button {
  display: flex;
  justify-content: space-evenly;
}
#contacts-container-right-button a {
  color: white;
  font-size: 1.5em;
  font-weight: bold;
  text-decoration: none;
  padding: 5px 10px;
  background-color: #0085dd;
  border-radius: 50px;
}

footer {
  text-align: center;
}
footer p {
  margin: 0;
  padding: 20px 0px;
}

@media only screen and (max-width: 1100px) {
  .part {
    padding: 20px 30px;
  }

  .part-title {
    color: #4f5b66;
    font-size: 1.5em;
    margin-bottom: 15px;
  }

  header #hambIcon {
    color: white;
    font-size: 20px;
    margin: 25px 15px;
    display: block;
    cursor: pointer;
    width: 25px;
  }
  header #link {
    position: fixed;
    top: 363px;
    left: 10px;
  }
  header #link hr.vertical {
    border: 0;
    border-right-color: currentcolor;
    border-right-style: none;
    border-right-width: 0px;
    border-right: 1px dashed #fff;
    color: #fff;
    height: 70px;
    width: 2px;
  }
  header #link svg {
    font-size: 20px;
  }
  header #link.active {
    opacity: 1;
    top: 45px;
    left: 350px;
  }

  #home #home-moreInfo {
    margin-top: 11rem;
  }
  #home #home-title {
    text-align: center;
    padding: 1em;
  }
  #home #home-title h1 {
    font-size: 3.5em;
    margin-top: 16rem;
    text-align: center;
  }
  #home #home-title h2 {
    font-size: 1.5em;
  }

  #home-button {
    margin-top: 255px;
  }

  #about {
    height: auto;
  }

  #about-container {
    display: flex;
    flex-direction: column;
  }
  #about-container img {
    margin: auto;
    width: 150px;
    height: 200px;
  }
  #about-container #about-text {
    text-align: left;
    font-size: 1em;
    line-height: 1.5em;
  }

  .project {
    min-width: 300px;
  }

  #contacts-container {
    display: flex;
    flex-direction: row;
  }

  #contacts-container-left, #contacts-container-right {
    width: 50%;
  }

  #contacts-container-left {
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  #contacts-container-left .contact-box {
    background-color: #c5cad5;
  }

  #contacts-container-right {
    text-align: center;
  }
  #contacts-container-right h3 {
    font-size: 1.5em;
  }

  #contacts-container-right-button a {
    background-color: #0085dd;
    border-radius: 50px;
    color: #fff;
    font-size: 1em;
    font-weight: 700;
    padding: 10px 31px;
    text-decoration: none;
  }
}
